import React from 'react';
import logo from './logo.svg';
import set3Images from './set3Images';
import set3ProbTable from './set3ProbabilityTable';
import set3ExpTable from './set3ExpTable';
import './App.css';

const emptyImg = set3Images[0].src;
let money = 50;
let level = 1;
let exp = 0;


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      money: 50,
      level: 1,
      exp: 0,
      time: 0
    }
    this.updateMoney = this.updateMoney.bind(this);
    this.updateLevel = this.updateLevel.bind(this);
    this.updateExp = this.updateExp.bind(this);
  }

  updateMoney(newMoney){
    this.setState({money: newMoney});
  }

  updateLevel(newLevel){
    this.setState({level: newLevel});
  }

  updateExp(newExp){
    this.setState({exp: newExp});
  }

  componentDidMount(){
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
    this.setState({refreshTiles: false});
    this.setState({
      time: this.state.time + 1
    });
    if(this.state.time % 5 == 0){
      exp+=2;
      money+= 5 + Math.min(5, Math.floor(money / 10));
      if(exp >= set3ExpTable[level - 1].exp){
        exp -= set3ExpTable[level - 1].exp;
        level++;
        this.updateLevel(level);
      }
      this.updateExp(exp);
      this.updateMoney(money);
    }
  }


  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            Edit <code>src/App.js</code> and save to reload.
          </p>
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn React
          </a>
          <p>Welcome to tft roller! Practice rolling for those shiny 3 stars and never facepalm for missing another champ!</p>
          <p>How to play: </p>
          <p>Every 5 seconds you will recieve 5 gold plus 1 gold interest for every 10 gold you have saved</p>
          <p>You can level up by pressing the buy exp button or by pressing f</p>
          <p>You can re-roll by pressing the re-roll button or by pressing d</p>
          <p>Your goal is to get the highest gold value for your comp. The comp will be randomly generated when you hit the Play button</p>
        </header>
        <div class="row">
          <InfoText info="Level" value={this.state.level}/>
          <InfoText info="Gold" value={this.state.money}/>
          <InfoText info="Exp" value={this.state.exp + '/' + set3ExpTable[level - 1].exp}/>
          <InfoText info="Time" value={this.state.time}/>
        </div>
        <ChampHand updateMoney={this.updateMoney} updateLevel={this.updateLevel} updateExp={this.updateExp}/>
      </div>
    )
  }
}


class InfoText extends React.Component {
  constructor(props){
    super(props);
  }

  render() {
    return (
      <h1>{this.props.info}: {this.props.value}</h1>
    )
  }
}

class ChampHand extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      tiles:[
        { id: 1, src: '/assets/set3/ahri.png', title: 'ahri', cost: 2, },
        { id: 2, src: '/assets/set3/annie.png', title: 'annie', cost: 2 },
        { id: 3, src: '/assets/set3/ashe.png', title: 'ashe', cost: 3 },
        { id: 4, src: '/assets/set3/aurelion-sol.png', title: 'aurelion-sol', cost: 5 },
        { id: 5, src: '/assets/set3/blitzcrank.png', title: 'blitzcrank', cost: 2 },
      ],
      refreshTiles: false
    };
    this.randomizeChamps = this.randomizeChamps.bind(this);

  }


   randomizeChamps(){
     //get the correct probabilty spread;
     let probSpread = set3ProbTable.filter((row)=>{
       return row.level === level;
     })[0];

     let probSpreadValues = Object.values(probSpread);

    //chooses 5 champs based on table probabilities and level
    let randomChamps = [];
    for(let i = 0; i < 5; i++){
      //roll from 1-100
      let randomNum = Math.floor(Math.random() * 100) + 1;
      let champCost = 0;
      //convert probspread to array to roll the random number through the prob probSpread
      for(let i = 1; i <= 5; i++){
        if(randomNum - probSpreadValues[i] <= 0){
          champCost = i;
          break;
        }else{
          randomNum -= probSpreadValues[i];
        }
      }
      //filter all the correct cost champs
      let set3CorrectCostChamps = set3Images.filter(champ =>
        champ.cost === champCost
      );

      randomChamps.push(set3CorrectCostChamps[Math.floor(Math.random() * set3CorrectCostChamps.length)]);
    }
    console.log(randomChamps);
    this.setState({tiles: randomChamps});
    this.setState({refreshTiles: true})
    // this.setState({tiles: [{ id: 1, src: '/assets/set3/ahri.png', title: 'ahri', cost: 2, }]});
    console.log(this.state.tiles);
  }

  componentDidMount(){
    //roll the first random randomChamps
    // alert("Hey");
    // this.randomizeChamps();
  }

  render() {
    return (
      <div>
        <ExpButton
        updateMoney={this.props.updateMoney} updateLevel={this.props.updateLevel} updateExp={this.props.updateExp}/>
        <RerollButton onReroll={this.randomizeChamps}
        updateMoney={this.props.updateMoney} updateLevel={this.props.updateLevel} updateExp={this.props.updateExp}/>
        {this.state.tiles.map((tile, i) => (
          <ChampTile key={i} src={process.env.PUBLIC_URL + tile.src} alt={tile.title} cost={tile.cost}
          updateMoney={this.props.updateMoney} updateLevel={this.props.updateLevel} updateExp={this.props.updateExp}/>
        ))}

      </div>
    )
  }
}



class RerollButton extends React.Component {
  constructor(props){
    super(props);
    this.state = {};
    this.reroll = this.reroll.bind(this);
  }

  reroll() {
    if(money >= 2){
      console.log("rerolling");
      money -= 2;
      this.props.updateMoney(money);
      this.props.onReroll();
    }
  }

  render() {
    return (
      <button onClick={this.reroll}>Reroll</button>
    )
  }
}

class ExpButton extends React.Component {
  constructor(props){
    super(props);
    this.state = {};
    this.buyExp = this.buyExp.bind(this);
  }

  buyExp() {
    if(money >= 4){
      console.log("buying exp");
      money -= 4;
      this.props.updateMoney(money);
      exp += 4;
      if(exp >= set3ExpTable[level - 1].exp){
        //level up
        exp -= set3ExpTable[level - 1].exp;
        level++;
        this.props.updateLevel(level);
      }
      this.props.updateExp(exp);
    }
  }

  render() {
    return (
      <button onClick={this.buyExp}>Buy Exp</button>
    )
  }
}

class ChampTile extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      hasChamp: true
    };
    this.buyChamp = this.buyChamp.bind(this);
  }

  buyChamp() {
    if(this.state.hasChamp && money >= this.props.cost){
      this.setState({hasChamp: false});
      money -= this.props.cost;
      this.props.updateMoney(money);
    }
  }

  componentWillReceiveProps({props}){
      this.setState({hasChamp: true});
  }

  render() {
    return (
      <button><img src={this.state.hasChamp ? this.props.src : process.env.PUBLIC_URL + emptyImg} alt={this.props.alt} onClick={this.buyChamp}/></button>
    )
  }
}

export default App;
